<template>
  <QueenNavBar text="成绩查询规则按照 姓名 或 班级编号查！例如：小仙女 或 701">
    <van-search ref="search"
        v-model="value"
        placeholder="小哥哥 or 小姐姐 名"
        show-action
        shape="round"
        @search="onSearch">
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>

    <van-collapse v-model="activeNames">
      <div v-for="(item,index) in students" :key="index">
        <van-collapse-item :name="index+1">
          <div slot="title">
            <van-row type="flex" justify="space-between">
              <van-col span="13">
                <van-tag plain type="primary">{{item.classNumber.substring(0,1)+'-'+item.classNumber.substring(1,3)}}</van-tag>
                {{item.name}}
              </van-col>
              <van-col span="11">
                <van-tag round type="success">班级：{{item.classRanking}}名</van-tag>
                <van-tag round type="danger">年级：{{item.totalScoreRanking}}名</van-tag>
              </van-col>
            </van-row>
          </div>
          <van-row type="flex" justify="center">
            <van-col span="6"> <van-tag plain type="success" size="medium">{{item.totalScore}}分</van-tag></van-col>
          </van-row>

          <van-row type="flex" justify="end">
            <van-col span="6">语文: {{item.chinese}}</van-col>
            <van-col span="6">数学: {{item.mathematics}}</van-col>
            <van-col span="6">英语: {{item.english}}</van-col>
            <van-col span="6">政治: {{item.politics}}</van-col>
          </van-row>
          <van-row type="flex" justify="end">
            <van-col span="6">历史: {{item.history}}</van-col>
            <van-col span="6">地理: {{item.geography}}</van-col>
            <van-col span="6">生物: {{item.biology}}</van-col>
            <van-col span="6">体育: {{item.sports}}</van-col>
          </van-row>
          <van-row type="flex" justify="end">
            <van-col span="6">语次: {{item.chineseRanking}}</van-col>
            <van-col span="6">数次: {{item.mathematicsRanking}}</van-col>
            <van-col span="6">英次: {{item.englishRanking}}</van-col>
            <van-col span="6">政次: {{item.politicsRanking}}</van-col>
          </van-row>
          <van-row type="flex" justify="end">
            <van-col span="6">历次: {{item.historyRanking}}</van-col>
            <van-col span="6">地次: {{item.geographyRanking}}</van-col>
            <van-col span="6">生次: {{item.biologyRanking}}</van-col>
            <van-col span="6">体次: {{item.sportsRanking}}</van-col>
          </van-row>

        </van-collapse-item>
      </div>
    </van-collapse>

  </QueenNavBar>
</template>

<script>
import {QueenNavBar} from '@comp'
import {studentData, studentDataByName,studentDataByClassOrName} from '@data'
export default {
  components:{QueenNavBar},
  name: 'QueenStudentScore',
  data(){
    return {
      studentData: studentData,
      studentDataByName: studentDataByName,
      studentDataByClassOrName: studentDataByClassOrName,
      value: "",
      students: {},
      activeNames: [] ,
      myG: ['小姐姐','张梦鸽','女王','憨大胆儿','小仙女'],
      my: ['小哥哥','郑亚锋','9527'],
    }
  },
  methods: {

    rest(){
      this.$refs.search.focus()
      this.students=[]
      this.value = ''
      return false;
    },
    source(){

    },
    onSearch(){
      if(this.myG.includes(this.value)){
        this.$toast("小可爱，你家9527拜见~~")
        return this.rest();
      }
      if(this.my.includes(this.value)){
        this.$toast("小可爱，稍等洗脚水马上给你端 哈哈~")
        return this.rest();
      }

      if(!this.studentDataByClassOrName[this.value]){
        this.$toast("名字你都记不住，重新输入！！！")
        return this.rest();
      }
      let data =[],cur=this.studentDataByClassOrName[this.value];
      if(!Array.isArray(cur)){
        data.push(cur)
      }else{
        data =cur
      }
      if(data.length>1){
        this.students = data.sort((a, b)=> b.totalScore - a.totalScore)
      }else{
        this.students = data
      }
    },
  }
}
</script>
